.App {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    padding-top: 100px;
}

.parameters, .counter {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 36px;
    font-weight: bold;
    width: 420px;
    height: 350px;
    border: #4789d5 solid 6px;
    border-radius: 30px;
}

.input_parameters {
    padding: 20px;
    width: 320px;
    height: 150px;
    border: #4789d5 solid 6px;
    border-radius: 30px;
    color: #4789d5;
}

.max_parameters, .min_parameters {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.min_parameters {
    padding-top: 40px;
}

.num {
    text-align: center;
    font-size: 100px;
    padding: 20px;
    width: 320px;
    height: 150px;
    border: #4789d5 solid 6px;
    border-radius: 30px;
    color: black;
}

.error {
    color: red;
}

.set, .buttonGroup {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
    padding: 0 20px;
    width: 320px;
    height: 50px;
    border: #4789d5 solid 6px;
    border-radius: 20px;
}

input {
    width: 80px;
    height: 30px;
    font-size: 30px;
    margin-left: 50px;
    border-radius: 10px;
    text-align: center;
}

button {
    height: 35px;
    width: 100px;
    border-radius: 10px;
    font-size: 24px;
    font-weight: bold;
    background-color: #4789d5;
}

